<template>
	<div id="category-good-list">
		<!--顶部菜单-->
		<nav-bar class="home-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				{{title}}
			</div>
			<div slot="right">
				图标
			</div>
		</nav-bar>
		<div class="flex">
			<!--左按钮-->
			<main-left-nav @leftBarClick="leftBarClick" ref="leftnav"/>
			<!--内容-->
			<main-right-content @rightScroll="rightScroll" :position="position" ref="mainright"/>
		</div>
	</div>
</template>

<script>
import NavBar from 'components/common/navbar/NavBar'

import MainLeftNav from './childComps/MainLeftNav'
import MainRightContent from './childComps/MainRightContent'


export default {
  name: 'CategoryGoodList',
  components: {
	NavBar,
	MainLeftNav,
	MainRightContent
  },
  data(){
	return{
		title:'商品列表',
		position:100,
		//储存各个组件的选项的位置
		contentArr:[],
		scrollHeight:0,		//可视高度
		allHeight:0,		//总高度
		lastHeight:0,		//最后的高度
		//currentScrollIndex:0,
		scrollIndex:0,
	}
  },
  methods:{
	//返回按钮
	backClick(){
		this.$router.go(-1)
	},
	//左边菜点击
	leftBarClick(index){
		//console.log(index)
		//怎么样改变main-right-content的移动
		//this.$refs.mainright.$refs.scroller.scroll.scrollTo(0,-100,300)
		//console.log(this.scrollHeight)
		//组件总高度-组件当前点击位置的高度<滚动的区域
		/*if(this.allHeight-(-this.contentArr[index])<this.scrollHeight){
			//总高度-可视高度=最大-的高度
			this.$refs.mainright.$refs.scroller.scroll.scrollTo(0,-(this.lastHeight),300)
		}else{
			if(this.contentArr[index])this.$refs.mainright.$refs.scroller.scroll.scrollTo(0,this.contentArr[index],300)
			console.log(this.contentArr[index])
		}*/
		//如果等于最后一个数字-预防溢出
		if(index>=(this.$refs.mainright.contentNavArr.length-1)){return}
		if(this.contentArr[index])this.$refs.mainright.$refs.scroller.scroll.scrollTo(0,this.contentArr[index],10)
	},
	//右边内容滚动
	rightScroll(position){
		//点击问题出现跳动-只要移动就会触发
		//position.y
		//右边滚动控制左边选中
		let positionY=position.y
		//循环判断滚动时的位置位于数组的那个区间
		for(let i=0;i<this.contentArr.length-1;i++){
			if(this.scrollIndex!==i&&(positionY<=this.contentArr[i]&&positionY>this.contentArr[i+1])){
				this.scrollIndex=i
				//问题出自这里
				this.$refs.leftnav.$refs.leftbar.currentIndex=this.scrollIndex
				console.log(i)
			}
		}
	},
  },
  mounted(){
	//这里获取上一个组件储存高度的数组
	//获取离顶部的高度的一个数组
	//console.log(this.$refs.mainright.contentNavArr)
	this.contentArr=this.$refs.mainright.contentNavArr
	//获取可视局域的高度
	this.scrollHeight=this.$refs.mainright.viewHeight
	//获取组件总高度
	this.allHeight=this.$refs.mainright.allHeight
	//获取-的高度最大值
	this.lastHeight=this.$refs.mainright.lastHeight
  },
  activated(){
	//进来的时候到达第二个位置
	//this.$refs.mainright.$refs.scroller.scroll.scrollTo(0,this.contentArr[0],0)
  }
}
</script >

<style>
  .icon-left{
	text-align:left;
	position:relative;
  }
  .icon-left img{
	position:absolute;
	top:10px;
	left:20px;
  }
  .flex{
	display:flex;
  }
  #category-good-list{
	overflow:hidden;
	height:100vh;
  }
</style>
