<template>
	<div class="nav-left-bar">
		<ul class="left-nav-box">
			<li v-for="(item,index) in testArr" :key="index"><i :class="{active:currentIndex==index}" @click="itemClick(index)">{{item}}</i></li>
		</ul>
	</div>
</template>

<script>

export default {
  name: 'NavLeftBar',
  components:{
  },
  data(){
	return{
		testArr:[				//外面给的
			'为您推荐',
			'内容',
			'规格',
			'详情',
			'创意风格',
		],
		currentIndex:0
	}
  },
  methods:{
	itemClick(index){
		if(this.currentIndex!==index){
			this.currentIndex=index
			this.$emit("leftBarClick",index)
		}
	}
  }
}
</script>


<style spoced>
i{
	font-style:normal;
}
.active{
	font-weight:bold;
	background:white;
	color:black;
}
.nav-left-bar{
	text-align:center;
}
.left-nav-box li i{
	line-height:55px;
	height:55px;
	display:block;
	overflow:hidden;
	font-size:14px;
}

</style>
