<template>
	<div class="main-content">
		<!--滚动--->
		<scroll class="content-scroller"
				ref="scroller"
				:probe-type=3
				@bscroll="bscroll"
		>
			<top-recommend-nav ref="recommend" @BoxItemClick="BoxItemClick"/>
			<!--内容全部-->
			<!--一个盒子 例如为你推荐-->
			<content-list-box :list-obj="contentListArr[0]" ref="content1" @BoxItemClick="BoxItemClick"/>
			<content-list-box :list-obj="contentListArr[1]" ref="content2" @BoxItemClick="BoxItemClick"/>
			<content-list-box :list-obj="contentListArr[2]" ref="content3" @BoxItemClick="BoxItemClick"/>
			<content-list-box :list-obj="contentListArr[3]" ref="content4" @BoxItemClick="BoxItemClick"/>
			<content-list-box :list-obj="contentListArr[3]" ref="content5" @BoxItemClick="BoxItemClick"/>
		</scroll>
	</div>
</template>

<script>

import Scroll from 'components/common/scroll/Scroll'
import TopRecommendNav from './TopRecommendNav'

import ContentListBox from 'components/contents/contentlistbox/ContentListBox'

export default {
  name: 'MainRigehtContent',
  components:{
	Scroll,
	TopRecommendNav,
	ContentListBox
  },
  data(){
	return{
		contentListArr:[
			{title:'为您推荐',itemArr:[{imgurl:'',title:'愉悦',color:'red'},{imgurl:'',title:'愉悦',color:'green'}]},
			{
				title:'内容',itemArr:[
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
				]
			},
			{title:'字体',itemArr:[{imgurl:'',title:'愉悦',color:'red'},{imgurl:'',title:'愉悦',color:'green'}]},
			{
				title:'内容',itemArr:[
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
					{imgurl:'',title:'愉悦',color:'red'},
				]
			},
			{title:'材质',itemArr:[{imgurl:'',title:'愉悦',color:'red'},{imgurl:'',title:'愉悦',color:'red'},{imgurl:'',title:'愉悦',color:'red'}]},
			{title:'材质',itemArr:[{imgurl:'',title:'愉悦',color:'red'},{imgurl:'',title:'愉悦',color:'red'},{imgurl:'',title:'愉悦',color:'red'}]},
			
		],
		//储存各个组件的选项的位置
		contentNavArr:[]
	}
  },
  props:{
	position:{
		type:Number,
		default:0,
		allHeight:0,		//滚动页面的高度
		viewHeight:0,		//可视区域的高度
		lastHeight:0,		//整个页面最后-的高度
	}
  },
  methods:{
	bscroll(position){
		this.$emit("rightScroll",position)
	},
	getNavTop(){
		//如果数据是从后台加载 图片高度获取不了
		this.contentNavArr.push(-this.$refs.content1.$el.offsetTop)
		this.contentNavArr.push(-this.$refs.content2.$el.offsetTop)
		this.contentNavArr.push(-this.$refs.content3.$el.offsetTop)
		this.contentNavArr.push(-this.$refs.content4.$el.offsetTop)
		this.contentNavArr.push(-this.$refs.content5.$el.offsetTop)
		this.contentNavArr.push(-1000000)		
		//console.log(this.contentNavArr)
	},
	//获取一个组件的高度参数：组件名称
	getComHeight(comName){
		return this.$refs[comName].$el.offsetHeight
	},
	//按钮点击
	BoxItemClick(index,item){
		console.log(index,item)
		this.$router.push("/gooddetail").catch(err=>{err})
	}
  },
  mounted(){
	this.getNavTop()
	//这个是滚动页面的总高度
	this.allHeight=this.getComHeight('recommend')
	this.allHeight+=this.getComHeight('content1')
	this.allHeight+=this.getComHeight('content2')
	this.allHeight+=this.getComHeight('content3')
	this.allHeight+=this.getComHeight('content4')
	this.allHeight+=this.getComHeight('content5')
	this.viewHeight=this.$refs.scroller.$el.offsetHeight			//这个可视区域的高度
	
	//滚动最后的高度不能超过-的视图高度
	//总高度-可视高度=最后-的高度
	this.lastHeight=this.allHeight-this.viewHeight
  }
}
</script>


