<template>
  <div class="content-list-box">
		<h3 class="title">{{listObj.title}}</h3>
		<div class="content-box">
			<!--这里是每个图片和标题-->
			<div class="item" v-for="(item,index) in itemArr" :key="index" @click="BoxItemClick(index,item.title)">
				<div class="img-box"></div>
				<p  :style="{color:item.color}">愉悦</p>
			</div>
		</div>
  </div>
</template>

<script>

  export default {
    name: "ContentListBox",
	components:{
		
	},
	props:{
		listObj:{
			type:Object,
			default(){
				return{}
			}
		}
	},
	computed:{
		itemArr(){
			return this.listObj.itemArr
		}
	},
	methods:{
		BoxItemClick(index,item){
			//其实这里传一个id进来的
			//console.log(index)
			//发送出去
			this.$emit("BoxItemClick",index,item)
		}
	}
  }
</script>

